.button__type-yellow{
    color: $permanent-blue-smoke;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.2;
    height: 40px;
    padding-left: 28px;
    padding-right: 28px;
    border: 1px solid $permanent-btn-yellow;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: transparent;
    transition: transform 0.5s ease;

    &:hover{
        background-color: $permanent-btn-yellow;
        color: $white;
        box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.25);
        transform: scale(1.1);
    }
}